import {
  AboutApi,
  AboutSaveApi,
} from '../../utils/adminApi';
import {
	Card,
  Button,
  message,
  Form,
  Input,
} from 'antd';
import { useEffect } from 'react';

const { TextArea } = Input;

const About = () => {
  const [form] = Form.useForm();

	useEffect(() => {
    AboutApi().then((res) => {
			form.setFieldsValue(res.data)
    });
	},[])

  const onFinish = (value) => {
    AboutSaveApi(value).then((res) => {
      message.success('保存成功');
    });
  };

	return (
<Card title="项目说明" bordered={false} style={{ width: 600 }}>
        <Form
          labelCol={{ span: 4 }}
          form={form}
          name="control-hooks"
          onFinish={onFinish}
        >
          <Form.Item name="id" style={{ display: 'none' }}>
            <Input />
          </Form.Item>
          <Form.Item name="name" label="标题" rules={[{ required: true }]}>
            <Input />
          </Form.Item>
          <Form.Item name="detail" label="详情">
            <TextArea rows={4} placeholder="输入公告内容" />
          </Form.Item>
          <Form.Item wrapperCol={{ span: 4, offset: 4 }}>
            <Button type="link" htmlType="submit">
              保存
            </Button>
          </Form.Item>
        </Form>
    </Card>
	)
}

export default About
